import React, { Component } from 'react';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';


import './style.css';
import TabsHRM from '../TabsHRM';
import * as Util from '../../state/Util';
// import CarouselHRM from '../carouselHRM';
import SlideCards from '../SlideCards';

import FiltersDetails from '../filtersDetails';
import { NavLink} from 'react-router-dom';
import Icons from '../Icons/Icons';
import Blocks from '../Blocks';
import Resume from '../Resume';
import RequestList from '../RequestList';
import Schedules from '../Schedules';
import * as commonActions from '../../state/commonActions';

import * as dialogActions from '../../state/dialogActions';

import QRCodeDialog from '../QRCodeComponent';
import InputCheckBox from '../CheckBox';

import InputText from '../InputText';


var notificationsWays = {
  "email":false,
  "sms":false,
  "webapp":false,
}

var processTimeObj = {
  "slow1":3000,
  "slow2":4000,
  "fast":4500,
}

const _tabs = ['filters' ,'schedules','deposit','resume','request'];
// getUserRequests(_port)
class Details extends Component {
  constructor(props) {
    super(props);  
    this.state = {
      visible :false,
      top:0,
      left:0,
      display:false,
      index:0,
      orientation:false,
      id:null
    };
  }
  componentDidMount() {  
    
  }  

  componentWillMount(){   
    window.localStorage.setItem('c_id',1); 
   
    var ind = null;
    
    /*
    _tabs.map((t,i)=>{
      if(t===section){
        ind=i;
      }
    })
*/
    const {location, history} = this.props;
    var s = Util.parseQuery(location.search);
    this.setState({id:s.usr});
    var userid = s.usr || window.localStorage.getItem('activeUser')
    if(userid){
      commonActions.UpdateUsersActive(userid);
    }
    if(s.tb){    
      _tabs.map((t,_i)=>{
        if(t===s.tb){
          ind=_i;
        }
      })
    } 



    if(ind){
      this.setState({index:ind});
    }
  }
  

  componentWillUnmount(){
    
  }

  UpdateIndex(i){
    this.setState({index:i});
    this.SldCard.updSlide(i)
  }

  goback(){      
     
  }




  open_QRCode(){     
    var formName = `logs_${'_QRCode'}`;    
    var _id = Util.Base64.encode(`_${formName}_`);
    let _IDUser = commonActions.getActiveUserDetail();
    const {store} = this.props
    const {userDetails} = store || {};
    var _cont = 
    <QRCodeDialog 
       ID={_IDUser}
       userDetails={userDetails}
       closeView={this.close_QRCode.bind(this,_id)}
       //checkForNewUser={this.checkForNewUser.bind(this)}
    />
    var options = {id:_id,zIndex:150,height:350,content:_cont};
    this.props.dialogActions.OpenView(options);
 }
 
 close_QRCode(_id){
    var options = {id:_id};
    this.props.dialogActions.CloseView(options);
 }
 



  open_SortOptions(){     
    var formName = `logs_${'sort'}`;    
    var _id = Util.Base64.encode(`_${formName}_`); 
    const {onlyActive} = this.state;
    var _cont = 
    <SortOptionLogsRX 
       _active={onlyActive}      
       changetheme={this.changetheme.bind(this)} 
       open_NotificationOptions={this.open_NotificationOptions.bind(this)}  
       processTime={this.open_ProccesOptions.bind(this)}
       //checkForNewUser={this.checkForNewUser.bind(this)}
    />
    var options = {id:_id,zIndex:150,height:150,content:_cont};
    this.props.dialogActions.OpenSlideOption(options);
 }
 
 close_SortOptions(){
    var formName = `logs_${'sort'}`;   
    var _id = Util.Base64.encode(`_${formName}_`); 
    var options = {id:_id};
    this.props.dialogActions.CloseSlideOption(options);
 }
 



  changetheme(){
    Util.changetheme();
    this.setState({themeSt:Util.gen6CodeId()});
    this.close_SortOptions();
  }
  
  sleepMode(_id){   
    let _IDUser = commonActions.getActiveUserDetail();
    var _v = {sleepMode:_id};
    commonActions.UpdateDetails(_IDUser,_v); 
    this.close_SortOptions();
  }

  ref = r => {
    this.SldCard = r
  }
  




  open_ProccesOptions(){     
    var formName = `prc_${'processTime'}`;    
    var _id = Util.Base64.encode(`_${formName}_`); 
    const {store} = this.props;
    const {userDetails } = store;
    var _cont = 
    <ProccesTimeOption
      userDetails={userDetails}
      _close={this.close_Options.bind(this,_id)}
     // updChange={this.updProcessType.bind(this)}
    />
    var options = {id:_id,zIndex:150,height:150,content:_cont};
    this.close_SortOptions();
    this.props.dialogActions.OpenSlideOption(options);
 }
 
 
 
 updProcessType(_id){
    let _IDUser = commonActions.getActiveUserDetail();
    var _v = {proccessTime:_id};
    commonActions.UpdateDetails(_v);
 }
 



  open_NotificationOptions(){     
    var formName = `ways_${'notifications'}`;    
    var _id = Util.Base64.encode(`_${formName}_`); 
    const {store} = this.props;
    const {userDetails } = store;     
    const { notificationTypes} = userDetails;
    var _notificationTypes = notificationTypes || notificationsWays;
    var _cont = 
    <NotificationTypesOption 
       notificationTypes={_notificationTypes}
       _close={this.close_Options.bind(this,_id)}
       updChange={this.updNotificationType.bind(this)}
    />
    var options = {id:_id,zIndex:150,height:350,content:_cont};
    this.close_SortOptions();
    this.props.dialogActions.OpenSlideOption(options);
 }
 
 
 
 updNotificationType(_id){
    let _IDUser = commonActions.getActiveUserDetail();
    var _v = {notificationTypes:_id};
    commonActions.UpdateDetails(_IDUser,_v);
 }
 
 
 
 close_Options(_id){
     let options = {id:_id};
     this.props.dialogActions.CloseSlideOption(options);
   }



  ref = r => {
    this.SldCard = r
  }
  
  render() {
    let _2link = {pathname: `/`};
    const {store} = this.props;
    const {userDetails } = store;
    var user = userDetails || {};
      return (
        <div className="_wrapper_com_">
          <div className={'__header__details'}>
               <NavLink  to={_2link} className="logo" ><div onClick={this.goback.bind(this)} className={'__back__icon'}><Icons name={'arrowBack'} color={'#1967d2'} size={32}/></div>  </NavLink>           
               <div className="flexSpace"/>                                     
            </div>
            <div className={`__items_setting details`} >
            <div className="email-title_detail"><span>{user.email || window.localStorage.getItem('_email_')}</span></div>   
            <div className="flexSpace"/>                          
            <div className={`filter_option smt`}>  
                <div onClick={this.open_SortOptions.bind(this)} >
                  <Icons name={'filter'}  size={24}/>
                </div> 
                <div className={`scanCode`} onClick={this.open_QRCode.bind(this)} >
                  <Icons name={'barCode'}  size={24}/>
                </div> 
              </div> 
             
          </div>
          <TabsHRM data={_tabs} UpdateIndex={this.UpdateIndex.bind(this)}  pth={'details'} usrQry={this.state.id} initValue={this.state.index}/>
          <SlideCards  ref={this.ref}>
            <FiltersDetails user={this.state.id}/>
            <Schedules k2show={'scheduleList'} title={'Schedule'}/>
            <Blocks k2show={'depositList'} title={'Deposit'}/>
            <Resume />
            <RequestList k2show={'requestList'} title={'RequestList'}/>
          </SlideCards>

        </div>
      ) 
  }
}







function mapStateToProps(state, ownProps) {
  return {   
    store:  Util.getStore(),  
    observeCommon: state.common.observeCommon,
    userDetails: state.common.userDetails,
    userPremium:state.common.userPremium,
  };
}


function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(commonActions, dispatch),  
    dialogActions: bindActionCreators(dialogActions, dispatch)
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Details);









class SortOptionLogs extends Component {
  constructor(props) {
    super(props);  
    this.state = {
      visible :false
    };
  }
  componentDidMount() {       

  }  

  componentWillUnmount(){
    
  }


  showFilters(i){      
     if(typeof this.props.showFilters === "function"){
        this.props.showFilters(i)
     }
  }

  
  changeSleepMode(i){ 
    //this.sleepMode(i)
    var _v = {sleepMode:i};
    commonActions.UpdateDetails(_v);
  }


  changetheme(){
   if(typeof this.props.changetheme === "function"){      
     this.props.changetheme()
   }
  }
  
  checkForNewUser(){
   if(typeof this.props.checkForNewUser === "function"){      
     this.props.checkForNewUser()
   }
  }


  open_NotificationOptions(){
   if(typeof this.props.open_NotificationOptions === "function"){      
     this.props.open_NotificationOptions()
   }
  }


  processTime(){
    if(typeof this.props.processTime === "function"){      
      this.props.processTime()
    }
   }


  sleepMode(_id){    
    var _v = {sleepMode:_id};
    commonActions.UpdateDetails(_v);
  }


  changeTurtleMode(_id){    
    var _v = {turtleMode:_id};
    commonActions.UpdateDetails(_v);
  }

  changespeedByHours(_id){    
    var _v = {speedByHours:_id};
    commonActions.UpdateDetails(_v);
  }

  
  

  logout(_id){    
    let _IDUser = commonActions.getActiveUserDetail(); 
    this.props.actions.removeCdaToken(_IDUser);   
  }



  ref = r => {
    this.MS_Elem = r
  }


  render() {    
    var theme = Util.getThemeMode()?'Dark Mode':'Light Mode';
    const {store} = this.props;
    const {userDetails } = store;        
    const {availableNotification, notificationTypes, sleepMode, turtleMode, speedByHours} =  userDetails || {};


    let isNot = false;
    if(availableNotification && notificationTypes){
     let hh = Util.ObjectKeys(notificationTypes).filter(a=>notificationTypes[a])
     hh.length>0?isNot=true:isNot=false;
    }

    return (
        <div className={'settings--wrapper'}>
          <div className={'__header--'}>
            <h5>
              Settings
            </h5>              
          </div>
          <div className="__body--"> 
             <div className={`__items_setting `} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
                 <div  className={'__icons--'}>
                    <Icons name={isNot?'notifications_active':'notifications_off'} color={isNot?'#ff7817':"#9c9c9c"} size={18} />
                           
                 </div>
                 <div  className={'__descr__'}>
                   {'Notification'}
                 </div>
                 <div className="flexSpace"/>  
                 <div className="actions--icons">
                  <div className={'notificationTypesActive'} onClick={this.open_NotificationOptions.bind(this)}>
                 {isNot?Util.ObjectKeys(notificationTypes).map((nTs,In3)=>{
                 
                   if(notificationTypes[nTs]){
                    return(
                      <span  className={''}>{nTs}</span>
                     )
                   }else{
                     return null
                   }
                   
                 }):<span  className={''}>{'disable'}</span>} 
                 </div></div>              
             </div>
             <div className={`__items_setting `} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
                 <div  className={'__icons--'}>
                    <Icons name={'moon'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'Sleep Mode'}
                 </div>
                 <div className="flexSpace"/>  
                 <div className="actions--icons">
                   <InputCheckBox icon={`more_vert`} field={`sleepMode`} updChange={this.changeSleepMode.bind(this)} initvalue={sleepMode}/>                  
                 </div>              
             </div>
             <div className={`__items_setting `} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
                 <div  className={'__icons--'}>
                    <Icons name={'tortoise'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'Turtle Mode'}
                 </div>
                 <div className="flexSpace"/>  
                 <div className="actions--icons">
                   <InputCheckBox icon={`more_vert`} field={`turtleMode`} updChange={this.changeTurtleMode.bind(this)} initvalue={turtleMode}/>                  
                 </div>              
             </div>
             <div className={`__items_setting `}  onClick={this.changetheme.bind(this)}>
                 <div  className={'__icons--'}>
                    <Icons name={'theme'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'Theme'}
                 </div>   
                 <div className="flexSpace"/>  
                 <div  className={'__descr__'}>
                   {theme}
                 </div>
             </div>
             <div className={`__items_setting `} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
                 <div  className={'__icons--'}>
                    <Icons name={'speedometer'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'Speed By Hours'}
                 </div>
                 <div className="flexSpace"/>  
                 <div className="actions--icons">
                   <InputCheckBox icon={`more_vert`} field={`speedByHours`} updChange={this.changespeedByHours.bind(this)} initvalue={speedByHours}/>                  
                 </div>              
             </div>
             <div className={`__items_setting `}  onClick={this.processTime.bind(this)}>
                 <div  className={'__icons--'}>
                    {<Icons name={'setting'} color={'#1967d2'} size={18}/>}
                 </div>
                 <div  className={'__descr__'}>
                   {'process Time'}
                 </div>   
                 <div className="flexSpace"/>  
                 <div  className={'__descr__'}>
                   
                 </div>
             </div>
             {/*
             <div className={`__items_setting `}  onClick={this.logout.bind(this)}>
                 <div  className={'__icons--'}>
                    <Icons name={'logout'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'Logout'}
                 </div>   
                 <div className="flexSpace"/>  
                 <div  className={'__descr__'}>
                   {''}
                 </div>
             </div>
              */}
          </div>          
        </div>
      ) 
  }
}



const SortOptionLogsRX = connect(mapStateToProps, mapDispatchToProps)(SortOptionLogs);






export class NotificationTypesOption extends Component {
  constructor(props) {
    super(props);  
    this.state = {
      visible :false
    };
  }
  componentDidMount() {       

  }  

  componentWillUnmount(){
    
  }


  updChange(fld,v){
     const { notificationTypes } = this.props; 
     var _ntfWay = notificationTypes;
     notificationTypes[fld] = v;
     if(typeof this.props.updChange === "function"){      
        this.props.updChange(_ntfWay)
     }
  }
  
  

  ref = r => {
    this.MS_Elem = r
  }


  render() {  
    const { notificationTypes } = this.props;     
    return (
        <div className={'settings--wrapper'}>
          <div className={'__header--'}>
            <h5>
              Notifications
            </h5>              
          </div>
          <div className="__body--"> 
             <div className={`__items_setting `} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
                 <div  className={'__icons--'}>
                    <Icons name={'email'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'Email'}
                 </div>
                 <div className="flexSpace"/>  
                 <div className="actions--icons">
                   <InputCheckBox icon={`more_vert`} field={`email`} updChange={this.updChange.bind(this,'email')} initvalue={notificationTypes.email}/>                  
                 </div>              
             </div>
             <div className={`__items_setting `} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
                 <div  className={'__icons--'}>
                    <Icons name={'sms'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'SMS'}
                 </div>
                 <div className="flexSpace"/>  
                 <div className="actions--icons">
                   <InputCheckBox icon={`more_vert`} field={`sms`} updChange={this.updChange.bind(this,'sms')} initvalue={notificationTypes.sms}/>                  
                 </div>              
             </div>
             <div className={`__items_setting `} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
                 <div  className={'__icons--'}>
                    <Icons name={'inAppMessage'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'Web App'}
                 </div>
                 <div className="flexSpace"/>  
                 <div className="actions--icons">
                   <InputCheckBox icon={`more_vert`} field={`webapp`} updChange={this.updChange.bind(this,'webapp')} initvalue={notificationTypes.webapp}/>                  
                 </div>              
             </div>
          </div>          
        </div>
      ) 
  }
}










export class ProccesTimeOption extends Component {
  constructor(props) {
    super(props);  
    this.state = {
      visible :false
    };
  }
  componentDidMount() {       

  }  

  componentWillUnmount(){
    
  }


  OnChange(fld,v){
    const { userDetails } = this.props; 
    const { processTime } = userDetails;
    var _processTime = processTime?processTime:processTimeObj;
    var _ntfWay = _processTime;
    _ntfWay[fld] = parseInt(v);
    var _v = {processTime:_ntfWay};
    commonActions.UpdateDetails(_v);
  }
  
  speedLightHandle(v){
    var _v = {speedLight: parseInt(v)};
    commonActions.UpdateDetails(_v);
 }


  ref = r => {
    this.MS_Elem = r
  }


  render() { 
    const { userDetails } = this.props; 
    const { processTime, speedLight } = userDetails;
    var _processTime = processTime?processTime:processTimeObj;
    return (
        <div className={'settings--wrapper'}>
          <div className={'__header--'}>
            <h5>
              Speed Process Time
            </h5>              
          </div>
          <div className="__body--"> 
             <div className={`__items_setting `} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
                 <div  className={'__icons--'}>
                    <Icons name={'hours'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'Slow 1'}
                 </div>
                 <div className="flexSpace"/>  
                 <div className="actions--icons">
                   <InputText icon={`more_vert`} field={`slow1`} OnChange={this.OnChange.bind(this,'slow1')} initvalue={_processTime.slow1}/>                  
                 </div>              
             </div>
             <div className={`__items_setting `} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
                 <div  className={'__icons--'}>
                    <Icons name={'hours'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'Slow 2'}
                 </div>
                 <div className="flexSpace"/>  
                 <div className="actions--icons">
                   <InputText icon={`more_vert`} field={`slow2`} OnChange={this.OnChange.bind(this,'slow2')} initvalue={_processTime.slow2}/>                  
                 </div>              
             </div>
             <div className={`__items_setting `} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
                 <div  className={'__icons--'}>
                    <Icons name={'hours'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'Fast'}
                 </div>
                 <div className="flexSpace"/>  
                 <div className="actions--icons">
                   <InputText icon={`more_vert`} field={`fast`} OnChange={this.OnChange.bind(this,'fast')} initvalue={_processTime.fast}/>                  
                 </div>              
             </div>
             <div className={`__items_setting `} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
                 <div  className={'__icons--'}>
                    <Icons name={'hours'} color={'#1967d2'} size={18}/>
                 </div>
                 <div  className={'__descr__'}>
                   {'Light'}
                 </div>
                 <div className="flexSpace"/>  
                 <div className="actions--icons">
                   <InputText icon={`more_vert`} field={`speedLight`} OnChange={this.speedLightHandle.bind(this)} initvalue={speedLight?speedLight:""}/>                  
                 </div>              
             </div>
          </div>          
        </div>
      ) 
  }
}






