function mapStateToProps(state, ownProps) {
   return {
      store:  Util.getStore(),  
      observeCommon: state.common.observeCommon,
      userPremium:state.common.userPremium,
   };
 }
 
 
 function mapDispatchToProps(dispatch) {
   return {
     actions: bindActionCreators(commonActions, dispatch),  
     dialogActions: bindActionCreators(dialogActions, dispatch)
   };
 }
 
 export default connect(mapStateToProps, mapDispatchToProps)(Filters);





 
class ItemFilter extends Component {
   constructor(props) {
     super(props);
     
     this.state = {
       active:false,
       minimunPay:0,
       editingpay:false,
       editingpayHour:false         
     };
   }
 
   componentWillMount() {  
      const {minimunPay} = this.props;     
      this.setState({minimunPay:minimunPay}); 
   }

   componentDidMount(){  
      const {form, item} = this.props;
      this.props.actions.UpdateFormbyName(form,item);
   }
   
   editingPay22(){
      this.setState({editingpay:!this.state.editingpay,edit:true});       
   }

   editingpayHour22(){
      this.setState({editingpayHour:!this.state.editingpayHour,edit:true});       
   }


   editingPay(){     
      const {item,form} = this.props;
      var formName = `filter_${'minimunPay'}`;    
      var _id = Util.Base64.encode(`_${formName}_`);
      var _cont = <ViewPaymentDetails SaveView={this.SaveMinimunPay.bind(this)} closeView={this.close_View.bind(this,_id)} item={item} _keyF={'minimunPay'} form={form} description={'Pago minimo deseado por bloque'} title={'Minimun Pay'}/>
      var options = {id:_id,zIndex:500,height:450,content:_cont};
      this.props.dialogActions.OpenView(options);
    }





   editingpayHour(){     
      const {item,form} = this.props;
      var formName = `filter_${'minimunPayByHour'}`;    
      var _id = Util.Base64.encode(`_${formName}_`);
      var _cont = <ViewPaymentDetails SaveView={this.SaveMinimunPay.bind(this)} closeView={this.close_View.bind(this,_id)} item={item} _keyF={'minimunPayByHour'} form={form} description={'Pago minimo deseado por hora'} title={'Minimun Pay by Hour'}/>
      var options = {id:_id,zIndex:500,height:450,content:_cont};
      this.props.dialogActions.OpenView(options);
    }


    close_View(_id){   
      var options = {id:_id};
      this.props.dialogActions.CloseView(options); 
    }
   


   SaveMinimunPay(){
      const {forms, form, filters} = this.props;
      var filter2Upd = forms[form]?forms[form]:null;
      if(filter2Upd){        
         var _filter = filters
         var ServiceAreas = filter2Upd.ServiceAreas;                
         //if(filters[ServiceAreas]['minimunPay']!==filter2Upd['minimunPay'] || filters[ServiceAreas]['minimunPayByHour']!==filter2Upd['minimunPayByHour']){  console.log('update changes') }else{ console.log('not changes')}         
         _filter[ServiceAreas] = filter2Upd;
         var _v = {filters:_filter};         
         commonActions.Update(_v);
      }   
      this.setState({editingpay:false,edit:false});
      this.setState({editingpayHour:false,edit:false});
   }



   OnChangePay(v){     
      const {form} = this.props;
      v = isNaN(parseFloat(v))?0:parseFloat(v);
      this.props.actions.UpdateForm(form,'minimunPay',v);      
   }

   OnChangePayByHour(v){
      const {form} = this.props;
      v = isNaN(parseFloat(v))?0:parseFloat(v);
      this.props.actions.UpdateForm(form,'minimunPayByHour',v);      
   }

   OnChangeActive(v){   
      const {forms,form,filters} = this.props;
      this.props.actions.UpdateForm(form,'active',v);
      var filter2Upd = forms[form]?forms[form]:null;
      if(filter2Upd){
         var _filter = filters
         var ServiceAreas = filter2Upd.ServiceAreas;
         _filter[ServiceAreas] = filter2Upd;         
         var _v = {filters:_filter};
         commonActions.Update(_v);
      }       
   }


   open_actionsOptions(i){    
      this.setState({id2edit:i});
      const {item} = this.props;     
      var formName = `options_${'Service_Area'}`;    
      var _id = Util.Base64.encode(`_${formName}_`); 
      var _cont = <SlideServiceAreaDetails _close={this.close_actionsOptions.bind(this,_id)} item={item}/>
      var options = {id:_id,zIndex:150,height:400,content:_cont};
      this.props.dialogActions.OpenSlideOption(options);
  
    }

    
   close_actionsOptions(i){  
      var options = {id:i};
      this.props.dialogActions.CloseSlideOption(options);
   }

   //
 
    
   render() {
     var _th6_ = this;
     const {forms,form,item,} = _th6_.props;     
     const {edit, editingpay, editingpayHour} = this.state;
     var minimunPayByHour = item.minimunPayByHour?item.minimunPayByHour:1;
     return(
      <div className="filter--cards ">
         <div className={`top--secction ${edit?'editing':''}`}>
            <div className="service--areas">
               <div className="label">
                  Service Area :
               </div>
               <div className="area"  onClick={this.open_actionsOptions.bind(this)}>
                  {item.name}
               </div>                          
            </div>    
            <div className={`actions`}>
               <div className={`${editingpay?'editingpay':''}`}>                               
                  <div className="pay-show"  onClick={this.editingPay.bind(this)}>
                        $ {item.minimunPay}
                  </div>
                  {/*<div className="edit-pay">
                     <span className="input--minimun--pay">
                        <InputText icon={`more_vert`} form={form} field={`minimunPay`} number={true} placeholder={'Minimun Pay'} OnChange={this.OnChangePay.bind(this)}  initvalue={item.minimunPay}/>                    
                     </span> 
                     <span className="btn--minimun--pay"> 
                        <div className="center--Container grayStyle" onClick={this.SaveMinimunPay.bind(this)} style={{"--color-tab--base--hover":'#777'}}>
                           <div className="hoverDiv orangeFlex "/>
                           <span className="text2D orangeFlex">{`Save`}</span>              
                        </div> 
                     </span>
                  </div> 
                  */}
               </div>  
               <div className={`${editingpayHour?'editingpayHour':''}`}>                                   
                  <div className="pay-show"  onClick={this.editingpayHour.bind(this)}>
                        $ {minimunPayByHour}
                  </div>                  
               </div>                                                            
            </div> 
         </div> 
         <div className={`active--secction ${edit?'editing':''}`}>
            <InputCheckBox icon={`more_vert`} forms={forms} form={form} field={`active`}  placeholder={'Active'} updChange={this.OnChangeActive.bind(this)} initvalue={item.active}/>            
         </div>                 
      </div>
     )
   }
 }


 
