 class Counter extends Component {
   constructor(props) {
     super(props);     
     this.state = {
       time:(new Date())  
     };
   }
 
   componentWillMount() {  
      var _thy = this;
      setInterval(()=>{
         _thy.setState({time:(new Date())});
      })
   }

   
   
    
   render() {
      const {time} = this.state;
      var now_ = time;
      let _hourly = `${parseTime(now_.getHours(),now_.getMinutes(),now_.getSeconds())}`;  
      return(
         <div className="_counter">
            <span>{_hourly}</span>         
         </div> 
      )
   }
 }






 


