 function parseTime(h,m,s) {
   //var _h = h-4<0?24+(h-4):h-4;
   var _h = h;
   var hrs = _h>12?`${_h-12}`:`${_h>9?_h:`0${_h}`}`;
   var min = m>9?`:${m}`:`:0${m}`;
   var sec = s>=0?s>9?`:${s}`:`:0${s}`:'';
   return `${hrs}${min}${sec} ${h>11?`PM`:`AM`}`;   
 }
 




 const _dayShortNames = ['Sun','Mon','Tue','Wed','Thu','Fri','Sat'];
 

 function ScheduleDaysActives(s) {
   var allWeek = true;
   var daysActive = [];
   s && Object.keys(s).map(day=>{
      if(s[day] && s[day]['active']){
         daysActive.push(_dayShortNames[day]);
      }else{
         allWeek = false;
      }
   })
   return allWeek?'Every day':daysActive.join(', ');
 }





