import React, { Component } from 'react';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as commonActions from '../../state/commonActions';
import * as dialogActions from '../../state/dialogActions';
import * as Util from '../../state/Util'; 
import Icons from '../Icons/Icons';

import './style.css';







 
function mapStateToProps2(state, ownProps) {
   return {
      forms: state.common.forms,
      formObserve: state.common.formObserve
   };
 }
 
 function mapDispatchToProps2(dispatch) {
   return {
     actions: bindActionCreators(commonActions, dispatch),
     dialogActions: bindActionCreators(dialogActions, dispatch)
   };
 }
 






 class OffersDetails extends Component {
   constructor(props) {
     super(props);     
     this.state = {
         editInput:false,
         editText:''
     };
   }
 
   componentWillMount() {  
      var _thy = this;
      
   }
   
  




   goback(){
      if(typeof this.props.closeView === "function"){
         this.props.closeView()
      }
   }


   

   render() {
      const {_list,_keyF,title,description} = this.props;
      const {editInput,editText} = this.state;
      const store = Util.getStore();
      const {userDetails } = store;    
      const {
        filters
      } = userDetails || {};
       
      return(
         <div className={'option__edit_payments'}>
            <div className={'__header__'}>
               {<div onClick={this.goback.bind(this)} className={'__back__icon'}><Icons name={'arrowBack'} color={'#1967d2'} size={24}/></div>}             
               <div className="flexSpace"/>                                      
            </div> 
            <div className="__body__">   
               <div className={'__title_body__'}>
                  {title}                 
               </div>
               <div className={'__title_description__'}>
                  {description}
               </div>
               <div className="filter--cards--wrapers">
                  {_list.map((bl,_ind)=>{
                     if(bl){
                        let amount = bl.rateInfo?bl.rateInfo.priceAmount:0;
                        let _hrs = (bl['endTime'] - bl['startTime'])/3600;   
                        let serviceArea = bl['serviceAreaId']; 
                        var flt = filters[serviceArea];  
                        console.log(flt)
                        return (
                                 <div className="filter--cards offers" key={Util.gen56CodeId()}>
                                    <div className={`top--secction `}>
                                       <div className="service--areas" style={{width:'60%'}}>               
                                          <div className="label date">    
                                          {date2pretyfy(bl['startTime']*1000)} - {time2pretyfy(bl['startTime']*1000)}        
                                          </div>                       
                                       </div>    
                                       <div className="actions" style={{width:'30%'}}>                                 
                                          <div className="pay-show"  >
                                                $ {amount}
                                          </div>                                                      
                                       </div>            
                                    </div>    
                                    <div className={`top--secction `}>
                                       <div className="_s_area" style={{width:'70%'}}>                                 
                                          <div className="label"  >
                                             {flt && flt["name"]}
                                          </div>                                                  
                                       </div>  
                                       <div className="hrs" style={{width:'20%'}}>               
                                          <div className="label">    
                                          {_hrs} HRS   
                                          </div>                       
                                       </div>    
                                                 
                                    </div>   
                                    <div className={`top--secction `}>
                                       <div className="_s_area" style={{width:'90%'}}>                                 
                                          <div className="label" style={{marginTop:"12px"}} >
                                             {`${flt && flt["address"]} ${flt && flt["city"]}`}
                                          </div>                                                   
                                       </div>
                                    </div>        
                                 </div>                
                        )
                     }else{
                        return null;
                     } 
                     }) 
                  }            
               </div>
            </div>
          </div> 
      )
   }
 }




 
 export default connect(mapStateToProps2, mapDispatchToProps2)(OffersDetails);


let hrs2Add = 0;

 function time2pretyfy(dt,ss) {
   var date = dt?!isNaN(dt)?new Date(parseInt(dt.toString())):new Date():new Date();
   date.setHours(date.getHours()+hrs2Add);
   var MM = date.getMinutes();
   var sec = date.getSeconds();
   var SS = ss?`:${sec>9?sec:`0${sec}`}`:'';
   return `${date.getHours()}:${MM>9?MM:`0${MM}`}${SS}`;
}



 function date2pretyfy(dt) {
   var date = !isNaN(dt)?new Date(parseInt(dt.toString())):new Date();   
   return `${Util.monthsList_Short[date.getMonth()+1]} ${date.getDate()}, ${date.getFullYear()}`;  
 }
