class UsersList extends Component {
  constructor(props) {
   super(props);  
   var date = new Date();
   date.setDate(date.getDate() - 1);
   this.state = {      
      active:0,
      asc:false,
      key:0,     
      _list:[],
      onlyActive:true,
      filters:{days:date.getTime()}             
    };
  }

   componentWillMount(){      
      //this.props.actions.getLogs();
      commonActions.LoadUsersActive();
      //this.props.actions.LoadUsersActive();
      
   }

  
  componentDidMount() { 
      window.localStorage.setItem('path',Util.Base64.encode('Users'));
      window.localStorage.setItem('c_id',9);  
      var date = new Date();
      date.setDate(date.getDate() - 1);
      this.setState({filters:{days:date.getTime()}});      
      //this.recalcdata(this.props.logs,filters,key,asc);
  }  


  componentWillReceiveProps(nextProps){
   const {filters,key,asc} = this.state;  
   const {observeCommon} = this.props;
   var _old = this.props.store.logs?this.props.store.logs.length:null;
   var _next = nextProps.store.logs?nextProps.store.logs.length:null;
   if(_old!==_next){
      this.recalcdata(nextProps.store.logs,filters,key,asc);
   }
 }


 recalcdata(oo,filters,key,asc){
    
   if(Array.isArray(oo)){         
      if(filters.days){            
         oo = oo.filter(d=>d['createdAt'] >= filters.days)
      }
      if(filters.user){
         oo = oo.filter(d=>d['user'] === filters.user)
      }
   }     
   
   var y={}
   oo.map((_cur)=>{   
      //var b64 = _cur.data?Util.Base64.decode(_cur.data):'';
      //console.log(_cur)
      var _5min = 60000*5;
      if(!y[parseInt(_cur.createdAt/_5min)] || y[parseInt(_cur.createdAt/_5min)]['status']!==_cur.status){
         y[parseInt(_cur.createdAt/_5min)] = _cur;
         y[parseInt(_cur.createdAt/_5min)]['qt'] = 1;
      }else{
         y[parseInt(_cur.createdAt/_5min)]['qt'] += 1;
      }        
   })  
   oo =  Util.convertObj2Array(y);
   var _key = sort_List[key];
   var _list_ = []
   if(Array.isArray(oo)){
      if(asc){
         _list_ = oo.sort(function(a, b) {
          if(a[_key] < b[_key]) { return -1; }
          if(a[_key] > b[_key]) { return 1; }
          return 0;          
        })
      }else{
         _list_ = oo.sort(function(a, b) {
          if(a[_key] > b[_key]) { return -1; }
          if(a[_key] < b[_key]) { return 1; }
          return 0;          
        })
      } 
   }     
  this.setState({_list:_list_});
}



  componentWillUnmount(){
      
  }


open_SortOptions(){     
   var formName = `logs_${'sort'}`;    
   var _id = Util.Base64.encode(`_${formName}_`); 
   const {onlyActive} = this.state;
   var _cont = 
   <SortOptionLogs 
      _active={onlyActive} 
      isSleepMode={this.props.store.sleepMode} 
      changetheme={this.changetheme.bind(this)} 
      showFilters={this.showFilters.bind(this)} 
      sleepMode={this.sleepMode.bind(this)} 
      checkForNewUser={this.checkForNewUser.bind(this)}
   />
   var options = {id:_id,zIndex:150,height:150,content:_cont};
   this.props.dialogActions.OpenSlideOption(options);
}

close_SortOptions(){
   var formName = `logs_${'sort'}`;   
   var _id = Util.Base64.encode(`_${formName}_`); 
   var options = {id:_id};
   this.props.dialogActions.CloseSlideOption(options);
}

changetheme(){
  Util.changetheme();
  this.close_SortOptions();
 }



 sleepMode(v){
  commonActions.sleepModeUser(v);
  //this.props.actions.sleepModeUser(v)
  this.close_SortOptions();
 }

 checkForNewUser(v){
  //this.props.actions.checkForNewUser();
  commonActions.checkForNewUser();
  this.close_SortOptions();
 }


  handleOptionsSortChanges(i){
   this.setState({asc:!this.state.asc,key:i})
   this.close_SortOptions();   
   this.recalcdata(this.props.store.logs,this.state.filter,i,!this.state.asc);
 }
   

 refresh_User_Active(){
  //this.props.actions.LoadUsersActive();
  //this.props.actions.RefreshDataUser();
  commonActions.LoadUsersActive();
  commonActions.RefreshDataUser();
 }

 reset_service(){
  //this.props.actions.ResetServer();
  commonActions.ResetServer();
 }


 showFilters(s){
  this.setState({onlyActive:s}) 
  this.close_SortOptions();
 }




 refFilter = r => {
   this._refFilter = r
 }

  render() {      
    const {store, observeCommon} = this.props;
    const {activeUserList } = store;
   const { onlyActive} = this.state;
      var _icon = this.state.asc?'arrow_down_large':'arrow_up_large';
      var key = sort_List[this.state.key];
      var _activeUserList = Util.convertObj2Array(activeUserList);
      var Show = _activeUserList?_activeUserList.length>0?true:false:false;
      var _activeUserListFltr =  onlyActive?_activeUserList.filter(g=>g.active):_activeUserList;
      
      var nn = 0;
      return (
         <div >
           
            <div className={'content_user'}/>            
            <div  className="blocks__sort" > 
              <div className="center--padding--btn-login">
                <div className="center--Container grayStyle" onClick={this.refresh_User_Active.bind(this)}>
                  <div className="hoverDiv orangeFlex "/>
                  <span className="text2D orangeFlex">{`Refresh`}</span>              
                </div>   
              </div>
              <div className="flexSpace"/>                           
              <div className={`filter_option `} onClick={this.open_SortOptions.bind(this)} >   
                <Icons name={'filter'}  size={24}/>
              </div>          
            </div>         
            <div className={`hirecGFkZElucHV0`} style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
               <div className="tab-label-title"><span>Users</span></div>               
               <div className="filter--cards--wrapers">
                  {Show?_activeUserListFltr.map((bl,_ind)=>{  
                        return (
                           <ItemRdx  item={bl} key={_ind}/>                        
                        )
                     }):<LoadingColorSpinner stroke={'var(--color-base--hover)'}/>
                  }         
               </div>
            </div>           
         </div>
      ) 
  }
}






function mapStateToProps(state, ownProps) {
   return {    
      store:  Util.getStore(),  
      observeCommon: state.common.observeCommon,  
      users:state.common.users, 
      sleepMode: state.common.sleepMode, 
      activeUserList:state.common.activeUserList,
      serviceAreas:state.common.serviceAreas,  
      formObserve: state.common.formObserve    
   };
 }
 

 function mapDispatchToProps(dispatch) {
   return {
     actions: bindActionCreators(commonActions, dispatch),     
     dialogActions: bindActionCreators(dialogActions, dispatch)
   };
 }
 
 export default connect(mapStateToProps, mapDispatchToProps)(UsersList);








 
