import React, { Component } from 'react';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as commonActions from '../../state/commonActions';
import * as dialogActions from '../../state/dialogActions';

import Icons from '../Icons/Icons';

import './style.css';







 
function mapStateToProps2(state, ownProps) {
   return {
      forms: state.common.forms,
      formObserve: state.common.formObserve
   };
 }
 
 function mapDispatchToProps2(dispatch) {
   return {
     actions: bindActionCreators(commonActions, dispatch),
     dialogActions: bindActionCreators(dialogActions, dispatch)
   };
 }
 






 class ViewPaymentDetails extends Component {
   constructor(props) {
     super(props);     
     this.state = {
         editInput:false,
         editText:''
     };
   }
 
   componentWillMount() {  
      var _thy = this;
      const {item,form,_keyF} = _thy.props;
      var v = item[_keyF];
      v = isNaN(parseFloat(v))?0:parseFloat(v);
      _thy.props.actions.UpdateForm(form,_keyF,v);
      _thy.setState({editText:v})
   }
   
   handleSaveEdit() {  
      var _thy = this;
      if(typeof this.props.SaveView === "function"){
         this.props.SaveView()
         _thy.goback()
      }
   }




   goback(){
      if(typeof this.props.closeView === "function"){
         this.props.closeView()
      }
   }


   _plus(){
      const {form,forms,_keyF} = this.props;
      var v = forms[form][_keyF];
      v = isNaN(parseFloat(v))?0:parseFloat(v);
      this.props.actions.UpdateForm(form,_keyF,v+1); 
      this.setState({editText:v+1})
      //console.log('_plus',v+1)
   }

   _minus(){      
      const {form,forms,_keyF} = this.props;
      var v = forms[form][_keyF];
      v = isNaN(parseFloat(v))?0:parseFloat(v);
      this.props.actions.UpdateForm(form,_keyF,v-1); 
      this.setState({editText:v-1})
     // console.log('_minus',v-1)
       
   }
    

   
   handleOpenEdit(){     
      this.setState({editInput:true}) 
      setTimeout(() => {
         var inF  = document.getElementsByTagName('input');
         var ria = inF[0];
         ria && ria.focus();         
      }, 20);
         
   } 

   handleBlur(){
      this.setState({editInput:false})    
   }
   
   handleChange(e){
      const {form,_keyF} = this.props;      
      var v = e.target.value; 
      v = isNaN(parseFloat(v))?0:parseFloat(v);
      this.props.actions.UpdateForm(form,_keyF,v);
      this.setState({editText:v})
      //console.log('handleChange',v)
   }

   handleKeyUp = e => {    
    if(e.keyCode){
      if(e.keyCode===13 || e.keyCode===27){    
         this.setState({editInput:false}) 
      } 
    }    
  };

   render() {
      const {item,_keyF,title,description} = this.props;
      const {editInput,editText} = this.state;
      return(
         <div className={'option__edit_payments'}>
            <div className={'__header__'}>
               {<div onClick={this.goback.bind(this)} className={'__back__icon'}><Icons name={'arrowBack'} color={'#1967d2'} size={24}/></div>}             
               <div className="flexSpace"/>                                      
            </div> 
            <div className="__body__">   
               <div className={'__title_body__'}>
                  {title}                 
               </div>
               <div className={'__title_description__'}>
                  {description}
               </div>
               <div className={'__minimun__pay__Wrapper__'}>
                  {editInput?
                     <div className={'__minimun__pay__'}>
                        <input type={'number'} value={editText} onChange={this.handleChange.bind(this)} onKeyUp={this.handleKeyUp.bind(this)}  onBlur={this.handleBlur.bind(this)}/>                     
                     </div>:
                     <div className={'__minimun__pay__'}  onClick={this.handleOpenEdit.bind(this)}>
                        {editText}
                     </div> 
                  } 
                  <div className={'__btns__'}>
                     <div className="flexSpace"/>
                     <div className={'__btn__'}  onClick={this._minus.bind(this)}>
                       <Icons name={'minus'} color={'#1967d2'} size={24}/>
                     </div>
                     <div className="flexSpace"/>
                     <div className={'__btn__'}  onClick={this._plus.bind(this)}>
                        <Icons name={'add'} color={'#1967d2'} size={24}/>
                     </div>
                     <div className="flexSpace"/>
                  </div>  
                  <div className="btnSavePayments" onClick={this.handleSaveEdit.bind(this)} style={{"--color-tab--base--hover":'#777'}}>                     
                     <span className="">{`Save`}</span>              
                  </div>                                
               </div>
            </div>
          </div> 
      )
   }
 }




 
 export default connect(mapStateToProps2, mapDispatchToProps2)(ViewPaymentDetails);




