function mapStateToProps(state, ownProps) {
   return {  
      store:  Util.getStore(),  
      observeCommon: state.common.observeCommon,   
      userDetails:state.common.userDetails,
      scheduleList:state.common.scheduleList,
      earningsList:state.common.earningsList  
   };
 }
 
 function mapDispatchToProps(dispatch) {
   return {
     actions: bindActionCreators(commonActions, dispatch),     
     dialogActions: bindActionCreators(dialogActions, dispatch)
   };
 }
 
 export default connect(mapStateToProps, mapDispatchToProps)(Blocks);








 
class ItemBlockCard extends Component {
   constructor(props) {
     super(props);
     
     this.state = {
       active:false,
       assign:false         
     };
   }
 
   componentWillMount() {      
     
   }

   componentDidMount(){  
      const {form, item} = this.props;
      this.props.actions.UpdateFormbyName(form,item);
   }
   
   editingPay(){
      const {users,_active} = this.props;     
      if(_active && users.length>1){
         this.setState({assign:!this.state.assign}) 
      }
     
   }

   assign2User(v){
      const {item} = this.props; 
      //this.props.actions.UpdateForm(form,'minimunPay',parseFloat(v));
      var assign = {block:item.id,tk:v.id};      
      if(assign){         
         this.setState({assign:false}); 
      }     
   }

   
    
   render() {
     
      var _th6_ = this;
      const {item,users,serviceAreas} = _th6_.props; 
      const store = Util.getStore();
      const {userDetails } = store;    
      const {
        filters
      } = userDetails || {};
       
      const {assign} = this.state;
  
      var _serviceArea = filters && filters[item['location']];
     return(
      <div className="filter--cards ">
         <div className={`top--secction ${assign?'editing':''}`}>
            <div className="hrs">                                 
               {`${item.hrs} HRS`}               
            </div>
         </div>
         <div className={`top--secction ${assign?'editing':''}`}>
            <div className="service--areas">               
               <div className="label date">    
                  {`${date2pretyfy(item['startTime'])} : ${time2pretyfy(item['startTime'])}`}        
               </div>
               <div className="area" style={{marginTop:"12px"}}>
                  {_serviceArea && _serviceArea["name"]}
               </div>                          
            </div> 
            <div className="actions">                                 
               <div className="pay-show" >
                     $ {item.pay}
               </div>                                                         
            </div>            
         </div>     
      </div>
     )
   }
 }


 
function mapStateToProps2(state, ownProps) {
   return {
      userDetails:state.common.userDetails,
      _active:state.common._active, 
      serviceAreas:state.common.serviceAreas, 
      forms: state.common.forms,
      formObserve: state.common.formObserve
   };
 }
 
 function mapDispatchToProps2(dispatch) {
   return {
     actions: bindActionCreators(commonActions, dispatch)
   };
 }
 
 export const ItemRdx = connect(mapStateToProps2, mapDispatchToProps2)(ItemBlockCard);



 



 export class SortOptionBlocks extends Component {
   constructor(props) {
     super(props);  
     this.state = {
       visible :false,
       active:0,
       asc:false      
     };
   }
   componentDidMount() {  
     
   }  
   componentWillUnmount(){
     
   }
 
 
   changeSort(i){
     if(this.state.active!==i){
       this.setState({active:i});
       this.props.updChange(i,this.state.asc)
     }else{
       this.setState({asc:!this.state.asc});
       if(typeof this.props.updChange === "function"){
         this.props.updChange(i,!this.state.asc)
       }
     }
     
   }
 
   ref = r => {
     this.MS_Elem = r
   }
   render() {  
     const { sortList, _value, _active } = this.props;   
     
     return (
         <div className={'option--wrapper'}>
           <div className={'__header--'}>
             <h5>
               Sort by
             </h5>              
           </div>
           <div className="__body--"> 
             {sortList?sortList.map((srt,indX)=>{
               var _icon = _value?'arrow_down_large':'arrow_up_large';
               return(
                 <div className={`__sort_options--  ${_active===indX?'__active':''}`} key={srt}  onClick={this.changeSort.bind(this,indX)}>
                   <div  className={'__icons--'}>
                     <Icons name={_icon} color={'#1967d2'} size={18}/>
                   </div>
                   <div  className={'__descr__'}>
                     {srt}
                   </div>                
                 </div>
               )
             })
             :null
             }
           </div>          
         </div>
       ) 
   }
 }
 
 
 





 class FilterOptionBlocks extends Component {
   constructor(props) {
     super(props);  
     this.state = {
       visible :false,
       active:null,
       sa:null,
       days:null
     };
   }
   componentDidMount() {  
     
   }  
   componentWillUnmount(){
     
   }
 
   clearFilters(i){
      this.setState({active:null,sa:null,days:null});     
      if(typeof this.props.updChange === "function"){
         var flt = {days:null,sa:null};
         this.props.updChange(flt)
      }  
   }
   
   goback(){
      this.setState({active:null});      
   }
 
   changeDate(){
      this.setState({active:1});      
   }
   changeSA(){
      this.setState({active:2});      
   }
   setfilterSa(s){
      this.setState({sa:s,active:null});  
      if(typeof this.props.updChange === "function"){         
         var flt = {sa:s};
         this.props.updChange(flt)
      }    
   }

   setfilterDay(s){
      this.setState({days:s,active:null});
      if(typeof this.props.updChange === "function"){
         var date = new Date();
         date.setDate(date.getDate() - s);
         var flt = {days:date.getTime(),dy2c:s};
         this.props.updChange(flt)
      }      
   }

   changeSort(i){
     
     
   }
   
   ref = r => {
     this.MS_Elem = r
   }
   render() {  
     const { serviceAreas,filters } = this.props;
     return (
         <div className={'option--wrapper'}>
           <div className={'__header--'}>
               {this.state.active?<div  onClick={this.goback.bind(this)} className={'__back__icon'}><Icons name={'arrowBack'} color={'#1967d2'} size={18}/></div>:<h5>Filters</h5>}               
               <div className="flexSpace"/>
               <div className={'__clear__btn'}>
                  <div className="center--Container grayStyle" onClick={this.clearFilters.bind(this)} style={{"--color-tab--base--hover":'#777'}}>
                     <div className="hoverDiv orangeFlex "/>
                     <span className="text2D orangeFlex">{`Clear`}</span>              
                  </div> 
               </div>           
           </div>
           <div className="__body--"> 
               <div className={`__action_options-- ${this.state.active===1?'__active':''}`}>
                  <div className={'__item_2_filters__'} onClick={this.changeDate.bind(this)}>
                     <div  className={'__icons--'}>
                        <Icons name={'calendar'} color={'#1967d2'} size={18}/>
                     </div>
                     <div className={'__descr__'}>
                        {'date'}
                     </div>   
                  </div>                 
                  <div className={'__days__filters__'}>
                     <div>                     
                        <div className={`__filter__items__ ${filters.dy2c===1?'__active':''}`}  onClick={this.setfilterDay.bind(this,1)}>
                           <div  className={'__icons--'}>
                              <Icons name={'calendar'} color={'#1967d2'} size={18}/>
                           </div>
                           <div  className={'__descr__'}>
                              {'Today'}
                           </div>
                        </div>
                        <div className={`__filter__items__ ${filters.dy2c===7?'__active':''}`}  onClick={this.setfilterDay.bind(this,7)}>
                           <div  className={'__icons--'}>
                              <Icons name={'calendar'} color={'#1967d2'} size={18}/>
                           </div>
                           <div  className={'__descr__'}>
                              {`last 7 days`}
                           </div>
                        </div>
                     </div>
                  </div>              
               </div>
               <div className={`__action_options--  ${this.state.active===2?'__active':''}`}>
                  <div className={'__item_2_filters__'} onClick={this.changeSA.bind(this)}>                  
                     <div  className={'__icons--'}>
                     <Icons name={'store'} color={'#1967d2'} size={18}/>
                     </div>
                     <div  className={'__descr__'}>
                        {'service area'}
                     </div> 
                  </div>
                  <div className={'__srvarea__filters__'}>
                     <div>
                     {Object.keys(serviceAreas).map((srva)=>{ 
                        return (
                        <div className={`__filter__items__ ${filters.sa===srva?'__active':''}`} key={srva} onClick={this.setfilterSa.bind(this,srva)}>
                           <div  className={'__icons--'}>
                              <Icons name={'store'} color={'#1967d2'} size={18}/>
                           </div>
                           <div  className={'__descr__'}>
                              {serviceAreas[srva].name}
                           </div>
                        </div>)
                     })
                     }
                        
                     </div>
                  </div>                
               </div>
           </div>          
         </div>
       ) 
   }
 }



 function download(content, fileName, contentType) {
   var a = document.createElement("a");
   var file = new Blob([content], {type: contentType});
   a.href = URL.createObjectURL(file);
   a.download = fileName;
   a.click();
 }

 var hrs2Add = 0;



 function parseTime(h,m) {  
   return `${h>12?`${h-12}`:`${h}`}:${m>9?`${m}`:`0${m}`}${h>11?`PM`:`AM`}`;   
 }

 function date2pretyfy(dt) {
   var date = !isNaN(dt)?new Date(parseInt(dt.toString())):new Date();   
   return `${Util.monthsList_Short[date.getMonth()+1]} ${date.getDate()}, ${date.getFullYear()}`;  
 }

 function time2pretyfy(dt) {
   var date = dt?!isNaN(dt)?new Date(parseInt(dt.toString())):new Date():new Date();   
   date.setHours(date.getHours()+hrs2Add);
   var MM = date.getMinutes();
   return `${date.getHours()}:${MM>9?MM:`0${MM}`}`;  
}



