import React, { Component } from 'react';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as commonActions from '../../state/commonActions';
import * as dialogActions from '../../state/dialogActions';
import InputText from '../InputText';
import InputCheckBox from '../CheckBox';
import * as Util from '../../state/Util';
import Icons from '../Icons/Icons';

import './style.css';







 
function mapStateToProps2(state, ownProps) {
   return {
      forms: state.common.forms,
      formObserve: state.common.formObserve
   };
 }
 
 function mapDispatchToProps2(dispatch) {
   return {
     actions: bindActionCreators(commonActions, dispatch),
     dialogActions: bindActionCreators(dialogActions, dispatch)
   };
 }
 





 class SlideScheduleDetails extends Component {
   constructor(props) {
     super(props);     
     this.state = {
       time:(new Date())  
     };
   }
 
   componentWillMount() {  
      var _thy = this;
     
   }
   
   
   handleSaveEdit() {  
      if(typeof this.props._close === "function"){
         this.props._close()
      }
   }




   goback(){
      if(typeof this.props._close === "function"){
         this.props._close()
      }
   }




   OpenHourlySettings(d,formName){      
         const {item} = this.props;           
         var _id = Util.Base64.encode(`_${formName}_`);
         var _cont = <ViewHourlyDetailsRdx SaveView={this.SaveHourly.bind(this)} closeView={this.close_View.bind(this,_id)} item={item[d]} _id={d} form={formName} description={`Horario en el que desea trabajar para el dia ${item[d]['dayName']}`} title={`Hourly for ${item[d]['dayName']}`}/>
         var options = {id:_id,zIndex:500,height:450,content:_cont};
         this.props.dialogActions.OpenView(options);
         this.goback();
   }
   
   
   close_View(_id){   
         var options = {id:_id};
         this.props.dialogActions.CloseView(options); 
   }
      
   SaveHourly(day,form,field){
      const {forms,item} = this.props;
      var filter2Upd = forms[form]?forms[form]:null;
      if(filter2Upd){
         var _filter = item;
         if( filter2Upd['start'] && _filter[day]['start']!==filter2Upd['start']){
            _filter[day]['start'] = filter2Upd['start'];
         }
         if( filter2Upd['end'] && _filter[day]['end']!==filter2Upd['end']){
            _filter[day]['end'] = filter2Upd['end'];
         }
         var _v = {schedule:_filter};         
         commonActions.Update(_v);
      }
   }  
    

   OnChangeActive(day,form,v){   
      const {forms,item} = this.props;
      this.props.actions.UpdateForm(form,'active',v);
      var filter2Upd = forms[form]?forms[form]:null;
      if(filter2Upd){
         var _filter = item;
         _filter[day]['active'] = filter2Upd['active'];     
         var _v = {schedule:_filter};         
         commonActions.Update(_v);
      }       
   }

    
   render() {
      const {item} = this.props;
      return(
         <div className={'option--wrapper'}>
            <div className={'__header--'}>                        
               <div className="flexSpace"/>
               <div className={'__save__btn '}>
               <div className="center--Container grayStyle" style={{"--color-tab--base--hover":'#777'}}>
                  <div className="hoverDiv orangeFlex "/>
                  <span className="text2D orangeFlex">{`Schedule`}</span>              
               </div> 
               </div>                        
            </div> 
            <div className="__body__" style={{'--checkBox--button--color':'#f2f2f2','--checkBox--button--Active--color':'#ff7817'}}>
            {item && Object.keys(item).map(dy=>{
               let day = item[dy];
               var formname = `schedule_day${dy}`;
               return(
                     <div className={`_squedule_day_item`} key={Util.gen6CodeId()}>
                        <div className={`active--secction`}>
                           <InputCheckBox icon={`more_vert`} form={formname} field={`active`}  placeholder={'Active'} updChange={this.OnChangeActive.bind(this,dy,formname)} initvalue={day.active}/>            
                        </div>                        
                        <div  className={'_squedule_day_Name__'}>
                           {day.dayName}
                        </div>  
                        <div className="flexSpace"/>
                        <div  className={'_squedule_day_Hour__ '} onClick={this.OpenHourlySettings.bind(this,dy,formname)} >
                           <Icons name={'hours'} color={'#4e67d2'} size={24}/>
                        </div>              
                     </div>
               )
            })}
            </div>
          </div> 
      )
   }
 }





 
 export default connect(mapStateToProps2, mapDispatchToProps2)(SlideScheduleDetails);





 class ViewHourlyDetails extends Component {
   constructor(props) {
     super(props);     
     this.state = {
         editInput:false,
     };
   }
 
   componentWillMount() {  
   }
   
   handleSaveEdit() {  
      var _thy = this;
      const {form,_id} = _thy.props;
      if(typeof _thy.props.SaveView === "function"){
         _thy.props.SaveView(_id,form,);
         _thy.goback()
      }
   }


   goback(){
      if(typeof this.props.closeView === "function"){
         this.props.closeView()
      }
   }

    

   render() {
      const {item,title,forms,form,description,_id} = this.props;
      return(
         <div className={'option__edit_payments'}>
            <div className={'__header__'}>
               {<div onClick={this.goback.bind(this)} className={'__back__icon'}><Icons name={'arrowBack'} color={'#1967d2'} size={24}/></div>}             
               <div className="flexSpace"/>                                      
            </div> 
            <div className="__body__">   
               <div className={'__title_body__'}>
                  {title}                 
               </div>
               <div className={'__title_description__'}>
                  {description}
               </div>
               <div  className={'__hourly__Wrapper__'}>
                  <HourlyUpdateComponent _keyF={`start`} form={form} item={item} forms={forms} actions={this.props.actions}/>
                  <HourlyUpdateComponent _keyF={`end`} form={form} item={item} forms={forms} actions={this.props.actions}/>
               </div>  
               <div className={'btnSave_Wrpp'}>                                
                  <div className="btnSavePayments" onClick={this.handleSaveEdit.bind(this,form,_id)} style={{"--color-tab--base--hover":'#777'}}>                     
                     <span className="">{`Save`}</span>              
                  </div>              
               </div> 
            </div>
          </div> 
      )
   }
 }




 
 export const ViewHourlyDetailsRdx = connect(mapStateToProps2, mapDispatchToProps2)(ViewHourlyDetails);






 

 class HourlyUpdateComponent extends Component {
   constructor(props) {
     super(props);     
     this.state = {
         editInput:false,
         editText:''
     };
   }
 
   componentWillMount() {  
      var _thy = this;
      const {item,_keyF} = _thy.props;
      var v = item[_keyF];
      v = isNaN(parseFloat(v))?0:parseFloat(v);
      _thy.setState({editText:v})
   }
   

   handleSaveEdit() {  
      var _thy = this;
      if(typeof this.props.SaveView === "function"){
         this.props.SaveView()
         _thy.goback()
      }
   }



   goback(){
      if(typeof this.props.closeView === "function"){
         this.props.closeView()
      }
   }


   _plus(){
      const {form,forms,item,_keyF} = this.props;      
      var v = forms[form]?forms[form][_keyF]?forms[form][_keyF]:item[_keyF]:item[_keyF];
      v = isNaN(parseFloat(v))?0:parseFloat(v);
      if(v<24){
         this.props.actions.UpdateForm(form,_keyF,v+1); 
         this.setState({editText:v+1})
      }
   }

   _minus(){
      const {form,forms,item,_keyF} = this.props;      
      var v = forms[form]?forms[form][_keyF]?forms[form][_keyF]:item[_keyF]:item[_keyF];
      v = isNaN(parseFloat(v))?0:parseFloat(v);
      if(v>2){
         this.props.actions.UpdateForm(form,_keyF,v-1); 
         this.setState({editText:v+1})
      }
   }
    


   render() {
      const {item,_keyF,form,forms} = this.props;
      var _valueT = forms[form]?forms[form][_keyF]?forms[form][_keyF]:item[_keyF]:item[_keyF];      
      return(
         <div className={'__minimun__pay__Wrapper__'}>  
            <div className={'___hourly__label__'}>
               {_keyF}
            </div>          
            <div className={'__minimun__pay__'}>
               {_valueT}
            </div> 
            <div className={'__btns__'}>
               <div className="flexSpace"/>
               <div className={'__btn__'}  onClick={this._minus.bind(this)}>
                  <Icons name={'minus'} color={'#1967d2'} size={24}/>
               </div>
               <div className="flexSpace"/>
               <div className={'__btn__'}  onClick={this._plus.bind(this)}>
                  <Icons name={'add'} color={'#1967d2'} size={24}/>
               </div>
               <div className="flexSpace"/>
            </div>                               
         </div>
      )
   }
 }

