

import React, { Component } from 'react';
import { withRouter,Switch,Route} from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as commonActions from '../state/commonActions';


import LoadingColorSpinner from './Icons/LoadingColorSpinner';

import Details from './Details';
import DetailAdmin from './DetailsAdmin';


import SlideOptionHRM from './SlideOptionHRM';
import DialogHRM from './DialogHRM';
import ViewHRM from './ViewHRM';

import Login from './Login';

import CodeToken from './CodeToken';
import AmazonLogin from './AmazonLogin';

import UserList from './usersList';


//import * as Subscription from '../state/subscription';

import {genId, getStore} from '../state/Util';

class Home extends Component {
  constructor(props) {
    super(props);
    
    this.state = {
      route_list : [],
      setting:false,
      badUser:false
    };
  }



  componentWillMount(){  
    this.resize = this.resize.bind(this);
    window.addEventListener('resize',this.resize);
    //Util.addScroll2();
    var _Id = genId();
    //console.log(_Id);
    this.props.commonActions.setSubId(_Id);
    //Subscription._EventSourceFunction(this.props.commonActions,_Id);
   
  }



  updateWidthScreen(){
    const isMobile = 720;  
    if(window.outerWidth<=isMobile){
      this.setState({ws:true});
      this.props.commonActions.UpdIsMobile(true);
    }
    else{
      this.setState({ws:false});
      this.props.commonActions.UpdIsMobile(false);
    }
  }



  componentWillUnmount(){
    window.removeEventListener('resize',this.resize);
  }

  componentDidMount(){
    this.props.commonActions.LoadData();
    this.updateWidthScreen();
    window.addEventListener('resize', this.updateWidthScreen.bind(this));  
  }

  resize(e){
    if(this.props.commonActions){
      this.props.commonActions.ScreenSize(window.outerWidth);
    }   
  }

  
  render() {
    const {store} = this.props;   
    const { appLoaded,authenticate,_active,isAdmin} = store; 
    if(appLoaded){
      if(authenticate){
        if(isAdmin){
          return (
            <div className={`App ${_active?'':'not_active'}`}>              
              <div id="content_body">    
                <Switch>
                  <Route exact path="/"  component={UserList} />
                  <Route path="/details"  component={DetailAdmin}/>
                </Switch>
              </div>  
              <SlideOptionHRM/>    
              <ViewHRM/>
              <DialogHRM/>    
            </div>
          );
        }else{
          return (
            <div className={`App ${_active?'':'not_active'}`}>              
              <div id="content_body">    
                <Switch>
                  <Route exact path="/"  component={Details} />
                  <Route path="/details"  component={Details} />
                  <Route path="/codeToken"  component={CodeToken} />
                </Switch>
              </div>  
              <SlideOptionHRM/>
              <ViewHRM/>        
            </div>
          );
        }
      }else{
        return (
        <div id="content_body">    
          <Switch>
            <Route exact path="/"  component={Login} />  
            <Route path="/codeToken"  component={CodeToken} />
            <Route path="/singupAmazon"  component={AmazonLogin} />
            
          </Switch>
        </div> 
       )
      }      
    }else{
      return (
        <div className="loadingScreen">
          <LoadingColorSpinner stroke={'#fff'}/>
        </div>
      )
    }
  }  
}


function mapStateToProps(state, ownProps) {
  return {       
    appLoaded: state.common.appLoaded,
    _active:state.common._active, 
    authenticate: state.common.authenticate,
    isAdmin: state.common.isAdmin,
    store:  getStore(), 
    observeCommon: state.common.observeCommon,
  };
}

function mapDispatchToProps(dispatch) {
  return {     
    commonActions: bindActionCreators(commonActions, dispatch)
  };
}


export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Home));


