export function LoadUserServiceInfo(email) {
    var doc = {email:email};
    const query= ` 
    query($doc: FindSchedule!){
      payload:  infoByUser(cda: $doc) {
        depositList{
          amount,
          date
        },
        scheduleList {
          pay,
          hrs,
          startTime,
          location,
        },
        earningsList{
          amount,
          tip,
          hrs,
          date
        }
      }
    }  
    `;
    let variables={doc};
    _Util.fetchGraphQL(GRAPHQLURL,{query,variables}) 
    .then(res => {
      if(res && res.data && res.data.payload){
        var f = res.data.payload;
        if(f["depositList"]){
          _Util.updStore('depositList',f["depositList"]);
        }
        if(f["earningsList"]){
          _Util.updStore('earningsList',f["earningsList"]);  
          filtersEarnings(f["earningsList"]);     
        }
        if(f["scheduleList"]){
          _Util.updStore('scheduleList',f["scheduleList"]); 
        }
        // _Util.updStore('appLoaded',true); 
        updObserveCommon();
      }
    }).catch(error => {      
      console.log(error);
    });
}




