import React, { Component } from 'react';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as commonActions from '../../state/commonActions';
import * as dialogActions from '../../state/dialogActions';

import Icons from '../Icons/Icons';

//import QRCode from 'qrcode-svg';

import * as Util from '../../state/Util';



import './style.css';







 
function mapStateToProps2(state, ownProps) {
   return {
      forms: state.common.forms,
      formObserve: state.common.formObserve
   };
 }
 
 function mapDispatchToProps2(dispatch) {
   return {
     actions: bindActionCreators(commonActions, dispatch),
     dialogActions: bindActionCreators(dialogActions, dispatch)
   };
 }
 






 class QRCodeDetails extends Component {
   constructor(props) {
     super(props);     
     this.state = {
         editInput:false,
         editText:''
     };
   }
 
   componentWillMount() {  
      var _thy = this;
      const {item,form,_keyF} = _thy.props;
      
   }
  




   goback(){
      if(typeof this.props.closeView === "function"){
         this.props.closeView()
      }
   }
   createMarkup(_qr) {
      return {__html:_qr};
    }



   render() {

      const {ID,userDetails} = this.props;
      var _ID = ID || '';
      const b64Data = Util.getBase64FromQRCode(_ID,{
         colorDark : "#030303",
         colorLight : "#f6f6f6"
      });
      var b64 = b64Data.split('data:image/png;base64,')[1];
      const blobUrl = Util.getBlob(b64)
      return(
         <div className={'option__edit_payments'}>
            <div className={'__header__'}>
               {<div onClick={this.goback.bind(this)} className={'__back__icon'}><Icons name={'arrowBack'} color={'#1967d2'} size={36}/></div>}             
               <div className="flexSpace"/>                                      
            </div>
            <div className="__body__">   
               <div className={'__title_QR__'}>
                  {userDetails && userDetails.email}                 
               </div>
               <div className={'__ID_QR__'}>
                  {ID}                 
               </div>
               <div className={'barCodeWrapper'}>
                
               <img src={blobUrl} alt="qrcode" className={'imgBarCode'}/> 
               </div>
            </div>
          </div> 
      )
   }
 }




 
 export default connect(mapStateToProps2, mapDispatchToProps2)(QRCodeDetails);




